import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { Container, Loader } from "semantic-ui-react";
import { Post as PostController} from "../../../api";
import "./Post.scss";

const postController= new PostController();

export function Post(){

    const [post1, setPost ] = useState(null);
    const{ post } = useParams();

    useEffect(()=>{
        ( async ()=> {
            try {
                const response = await postController.getPost(post);
                setPost(response);
            } catch (error) {
                console.error(error);
            }
        })();
    },[post])

    if(!post1) return <Loader active inline="centered" />

    return(
        <Container className="post">
            <h1 className="title">{post1.title}</h1>
            <div 
                className="content"
                dangerouslySetInnerHTML={{__html: post1.content}}
            />
            
        </Container>
    );
}